import React, { useState } from "react";
import "../assets/css/project.css";
import { projects } from "../utils/portfolio";
import ProjectCard from "./ProjectCard";

function Project() {
  const [modal, setModal] = useState<any>({
    visible: false,
    data: null
  });
  return (
    <section className="section--project" id="project">
      <div className="inner">
        <div className="project-title">
          <p>{projects.title}</p>
        </div>
        <div className="project-subtitle">
          <p>{projects.subTitle}</p>
        </div>

        <div className="project-cards">
          {projects.lists.map((list, i) => {
            return <ProjectCard project={list} key={i} setModal={setModal} />;
          })}
        </div>
      </div>
      {modal.visible && (
        <div className="modal-wrapper">
          <div className="modal-background" onClick={() => setModal({ visible: false, data: null })}>
            <div className="modal-card" onClick={(e: any) => e.stopPropagation()}>
              <h2>
                {modal.data.title}{" "}
                {modal.data?.status && (
                  <a
                    href={modal.data.url}
                    target="_blank"
                    rel="noreferrer"
                    onClick={(e: any) => {
                      if (!modal.data?.status) e.preventDefault();
                    }}
                  >
                    (Link)
                  </a>
                )}
              </h2>
              <ul>
                {modal.data.image?.map((item: any, index: number) => (
                  <li key={`img_${index}`}>
                    <img src={item} alt="" />
                  </li>
                ))}
                {modal.data.big_image?.map((item: any, index: number) => (
                  <li key={`big_img_${index}`}>
                    <img src={item} alt="" className="big-image" />
                  </li>
                ))}
              </ul>

              <div className="tag-wrapper">
                {modal.data.tag?.map((item: any, index: number) => (
                  <span key={`tag_${index}`} className="modal-tag">
                    {item}
                  </span>
                ))}
              </div>
              <p className="modal-description">{modal.data.desc}</p>
              {modal.data?.desc_en && <p className="modal-description">{modal.data?.desc_en}</p>}
            </div>
          </div>
        </div>
      )}
    </section>
  );
}
export default Project;
